					phone: '13800138000',
					manager: 'ÁéãÁªèÁêÜ',
					department: 'Ê∂àË¥π‰ø°Ë¥∑ÈÉ®',
					product: '‰∏™‰∫∫Ê∂àË¥πË¥∑',
					loanStatus: 'ÊâπÊ¨æ',
					amount: '10‰∏á',
					rejectReason: '',
					age: 35,
					marriageStatus: 'married',
					customerGroup: 'Ê∂àË¥π',
					workplace: 'Âåó‰∫¨ÁßëÊäÄÊúâÈôêÂÖ¨Âè∏',
					income: '20000ÂÖÉ/Êúà',
					jindu: 'pending',
					status: 'intention', // ÊÑèÂêëÂÆ¢Êà∑
					creditDescription: '‰ø°Áî®ËâØÂ•ΩÔºåÊó†ÈÄæÊúü',
					remarks: 'ÂÆ¢Êà∑ÂØπ‰∫ßÂìÅÂæàÊÑüÂÖ¥Ë∂£',
					createTime: '2024-03-14 10:25:36',
					updateTime: '2024-03-15 15:42:18'
				},
				{
					id: 2,
					name: 'ÊùéÂõõ',
					phone: '13800138001',
					manager: 'ËµµÁªèÁêÜ',
					department: 'Â∞èÂæÆ‰ø°Ë¥∑ÈÉ®',
					product: 'Â∞èÂæÆ‰ºÅ‰∏öË¥∑',
					loanStatus: 'ÊîæÊ¨æ',
					amount: '20‰∏á',
					rejectReason: '',
					age: 42,
					marriageStatus: 'married',
					customerGroup: 'ÁªèËê•',
					licenseInfo: 'Âåó‰∫¨È£üÂìÅË¥∏ÊòìÊúâÈôêÂÖ¨Âè∏',
					licenseImages: ['https://example.com/license1.jpg'],
					income: '50000ÂÖÉ/Êúà',
					creditDescription: '‰ø°Áî®‰ºòËâØÔºåÂéÜÂè≤Ë¥∑Ê¨æÊåâÊó∂ËøòÊ¨æ',
					jindu: 'approved',
					status: 'deal', // Â∑≤Êàê‰∫§
					remarks: 'ËÄÅÂÆ¢Êà∑ÔºåÂ∑≤Â§öÊ¨°Âêà‰Ωú',
					createTime: '2024-03-10 09:15:22',
					updateTime: '2024-03-13 17:30:45'
