				'intention': 'Êú™ËÅîÁ≥ª',
				'deal': 'ÂÆ¢Êà∑Âà∞ËÆø'
			};
			return map[status] || status;
		},
		getJinduText(jindu) {
			const map = {
				'pending': 'ÂæÖÂÆ°Êâπ',
				'approved': 'Â∑≤ÈÄöËøá',
				'rejected': 'Â∑≤ÊãíÁªù'
			};
			return map[jindu] || jindu;
		},
		formatDate(timestamp) {
			if (!timestamp) return '';
			const date = new Date(timestamp);
			return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
		},
		goDetail(item) {
			uni.navigateTo({
